.sections {
  margin-left: 13px;

  @media (max-width: $tablet-width) {
    margin-top: 35px;
    margin-left: 20px;
    letter-spacing: -0.5px;
  }

  @media (min-width: $min-width) and (max-width: $mobile-width) {
    position: relative;
    margin-top: 42px;
    margin-left: 0;
    padding-left: 10px;
    padding-bottom: 11px;
  }
}

.sections h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  font-weight: 400;
  text-transform: uppercase;
}

.sections ul {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-top: 22px;
  height: 100px;

  list-style: none;
  padding: 0;

  font-size: 15px;
  line-height: 25px;

  @media (max-width: $tablet-width) {
    height: auto;
  }
}

.sections li {
  width: 50%;

  @media (max-width: $tablet-width){
    width: 100%;
  }
}

.sections::after {
  @media (min-width: $min-width) and (max-width: $mobile-width) {
    content: "";
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    height: 1px;
    background-color: $lines;
  }
}
